<template>
  <div>
    <v-toolbar color="rgba(0,0,0,0)" flat>
      <v-toolbar-title class="mt-n3">Choose Category</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-text-field
        label="Search category or menu..."
        class="mt-5"
        color="brown"
        filled
        append-icon="mdi-magnify"
        dense
        solo
        flat
        background-color="grey lighten-4"
        v-model="inputSearch"
        @click:append="toSearch"
      >
      </v-text-field>
    </v-toolbar>
    <v-item-group mandatory-class="mt-n1">
      <v-container>
        <v-row justify="center">
          <v-col col="12" md="2" v-for="(category, i) in categories" :key="i">
            <v-item v-slot="{ active, toggle }">
              <v-card
                :color="active ? '#F6EFEF' : 'white'"
                :class="active ? 'borderme' : 'borderout'"
                dark
                height="140"
                @click="toggle"
                flat
              >
                <v-row>
                  <v-col col="12" sm="12" @click="showMenu(category.id)">
                    <v-list-item
                      three-line
                      class="text-center"
                      style="padding: 0px !important"
                    >
                      <v-list-item-content style="padding: 0px !important">
                        <div align="center" justify="center">
                          <img
                            :src="`${category.img}`"
                            height="110vh"
                            width="150vw"
                          />
                        </div>
                        <v-list-item-subtitle
                          :class="active ? 'brown--text' : 'black--text'"
                          class="caption"
                        >
                          {{ category.title }}
                        </v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                  </v-col>
                </v-row>
              </v-card>
            </v-item>
          </v-col>
        </v-row>
      </v-container>
    </v-item-group>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Categories",
  data() {
    return {
      inputSearch: "",
    };
  },
  computed: {
    ...mapGetters({ categories: "categories" }),
  },
  methods: {
    showMenu(id) {
      this.$emit("showMenu", id);
    },
    toSearch() {
      this.$emit("inputSearch", this.inputSearch);
    },
  },
};
</script>

<style></style>
