const mutations = {
  setCart(state, cart) {
    state.cart.push(cart);
  },
  setDeleteIdCart(state, id) {
    state.cart = state.cart.filter((item) => item.id !== id);
  },
  setAddCartItemAmount(state, id) {
    state.cart = state.cart.map((item) => {
      if (item.id === id) {
        console.log(item.id, " = ", id);
        if (item.count > 19) return item;
        item.count = item.count + 1;
      }
      return item;
    });
  },
  setRemoveCartItemAmount(state, id) {
    state.cart = state.cart.map((item) => {
      if (item.id === id) {
        if (item.count < 2) return item;
        item.count = item.count - 1;
      }
      return item;
    });
  },
  setRemoveCart(state) {
    state.cart = [];
  },
};

const actions = {
  addToCart({ commit }, product) {
    commit("setCart", product);
  },
  deleteFromCart({ commit }, id) {
    commit("setDeleteIdCart", id);
  },
  addAmount({ commit }, id) {
    commit("setAddCartItemAmount", id);
  },
  removeAmount({ commit }, id) {
    commit("setRemoveCartItemAmount", id);
  },
  removeCart({ commit }) {
    commit("setRemoveCart");
  },
};

const getters = {
  cart: ({ cart }) => cart,
};

const state = () => ({
  cart: [],
});

export default {
  mutations,
  getters,
  actions,
  state,
};
