<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background }">
    <v-container>
      <v-toolbar color="#EEEEEE" flat>
        <v-toolbar-title>History Bills</v-toolbar-title>
      </v-toolbar>
      <div>
        <v-list
          v-for="(bills, i) in historyBills"
          :key="i"
          subheader
          two-line
          class="mt-1"
          >Order № {{ i + 1 }}
          <v-list-item v-for="(bill, k) in bills" :key="k">
            <v-list-item-avatar
              rounded
              color="grey lighten-4"
              style="width: 60px !important; height: 60px !important"
            >
              <v-img :src="`${bill.img}`"></v-img>
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title
                class="subtitle-2"
                :title="bill.title + ' ' + bill.size"
                >{{ bill.title }} '{{ bill.size }}'</v-list-item-title
              ><v-list-item-title
                v-if="bill.category === 'Coffee'"
                class="subtitle-2"
                :title="bill.temp"
                >Temperatute: {{ bill.temp.toUpperCase() }}</v-list-item-title
              >
              <v-list-item-title
                v-if="bill.category !== 'Juice' && bill.category !== 'Dessert'"
                class="subtitle-2"
                :title="bill.sugar"
                >Sugar: {{ bill.sugar }}%</v-list-item-title
              >
              <v-list-item-subtitle
                >&#9747;{{ bill.count }}
              </v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-action
              class="caption"
              style="
                font-size: 14px !important;
                margin-left: 0;
                align-self: right;
              "
            >
              ${{
                parseFloat(bill.priceDynamic * bill.count).toFixed(2)
              }}</v-list-item-action
            >
          </v-list-item>
          <hr />
        </v-list>
      </div>
    </v-container>
  </v-app>
</template>

<script>
export default {
  computed: {
    historyBills() {
      console.log("history=", this.$store.state.historyCart.historyCart);
      return this.$store.state.historyCart.historyCart;
    },
  },
};
</script>

