<template>
  <v-navigation-drawer app color="white" right width="290">
    <v-list subheader two-line class="mt-1">
      <v-list-item>
        <v-list-item-avatar rounded>
          <v-img src="https://cdn.vuetifyjs.com/images/lists/1.jpg"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-subtitle>I'm a Cashier!</v-list-item-subtitle>
          <v-list-item-title>Jelly Grande</v-list-item-title>
        </v-list-item-content>
        <v-list-item-action>
          <v-badge bordered overlap color="red" dot>
            <v-icon>far fa-bell</v-icon>
          </v-badge>
        </v-list-item-action>
      </v-list-item>
    </v-list>
    <div v-if="cart.length > 0">
      <strong class="ml-3">Bills</strong>

      <v-list
        v-for="(bill, i) in cart"
        :key="i"
        subheader
        two-line
        class="mt-1"
      >
        <v-list-item>
          <v-list-item-avatar
            rounded
            color="grey lighten-4"
            style="width: 60px !important; height: 60px !important"
          >
            <v-img :src="`${bill.img}`"></v-img>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title
              class="subtitle-2"
              :title="bill.title + ' ' + bill.size"
              >{{ bill.title }} '{{ bill.size }}'</v-list-item-title
            >
            <v-btn
              color="#704232"
              class="small-btn"
              dark
              @click="addAmount(bill.id)"
              >&plus;</v-btn
            ><v-btn
              color="#704232"
              class="small-btn"
              dark
              @click="removeAmount(bill.id)"
              >&minus;</v-btn
            >
            <v-list-item-subtitle
              >&#9747;{{ bill.count }}

              <v-btn plain color="#704232" small
                >Notes
                <v-icon right>mdi-pencil</v-icon>
              </v-btn>
            </v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-action
            class="caption"
            style="
              font-size: 14px !important;
              margin-left: 0;
              align-self: right;
            "
          >
            ${{
              parseFloat(bill.priceDynamic * bill.count).toFixed(2)
            }}</v-list-item-action
          >
          <v-btn
            class="small-btn mt-3"
            color="#704232"
            dark
            @click="deleteFromCart(bill.id)"
          >
            <span class="small-btn-item">x</span>
          </v-btn>
        </v-list-item>
      </v-list>

      <v-toolbar color="rgba(0,0,0,0)" flat>
        <strong>Subtotal</strong><v-spacer></v-spacer
        ><span>${{ subTotal }}</span>
      </v-toolbar>
      <v-toolbar color="rgba(0,0,0,0)" flat class="mt-n6">
        <strong>Tax(10%)</strong><v-spacer></v-spacer><span>${{ tax }}</span>
      </v-toolbar>
      <v-divider class="mx-4"></v-divider>
      <v-toolbar color="rgba(0,0,0,0)" flat>
        <strong>Total</strong><v-spacer></v-spacer><span>${{ total }}</span>
      </v-toolbar>
      <strong class="ml-5">Payment Method</strong>
      <v-item-group mandatory class="mt-n1">
        <v-container>
          <v-row justify="center">
            <v-col
              cols="12"
              md="4"
              v-for="(payment, i) in $options.paymentTypes"
              :key="i"
            >
              <v-item v-slot="{ active, toggle }">
                <v-card
                  color="#F6EFEF"
                  :class="active ? 'borderme' : ''"
                  class="d-flex align-center rounded-lg"
                  dark
                  height="70"
                  @click="addPayment(toggle, payment.title)"
                  flat
                >
                  <v-row>
                    <v-col cols="12" md="12">
                      <v-list-item three-line class="text-center mt-1">
                        <v-list-item-content>
                          <div>
                            <v-icon :color="active ? '#703232' : 'black'">{{
                              payment.iconName
                            }}</v-icon>
                          </div>
                          <v-list-item-subtitle
                            :class="active ? 'brown--text' : 'black--text'"
                            class="mt-n2 caption"
                            >{{ payment.title }}</v-list-item-subtitle
                          >
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                  </v-row>
                </v-card>
              </v-item>
            </v-col>
          </v-row>
        </v-container>
      </v-item-group>
      <div class="mx-3 mt-2">
        <v-btn
          color="#704232"
          block
          dark
          class="widthoutupercase"
          @click="dialog = true"
          >Print Bills</v-btn
        >
      </div>
    </div>
    <div v-else>
      <strong class="ml-3"> Your cart is empty... </strong>
    </div>
    <v-row justify="center">
      <v-dialog v-model="dialog" persistent max-width="290">
        <v-card>
          <v-card-title
            class="text-h5"
            style="color: white; background-color: #704232"
          >
            Are you sure in order?
          </v-card-title>
          <hr />
          <div v-for="(product, i) in cart" :key="i" class="ml-2">
            <v-card-text style="padding: 0px !important">
              <strong>Title:</strong> {{ product.title }}
              {{ product.size }}</v-card-text
            >
            <v-card-text style="padding: 0px !important">
              <strong>Temperature:</strong> {{ product.temp }}</v-card-text
            >
            <v-card-text style="padding: 0px !important">
              <strong>Sugar:</strong> {{ product.sugar }}%</v-card-text
            >
            <v-card-text style="padding: 0px !important">
              <strong>Amount:</strong> X{{ product.count }}</v-card-text
            >
            <v-card-text style="padding: 0px !important"
              ><strong>Price:</strong> ${{ product.priceDynamic }}</v-card-text
            >
            <hr />
          </div>

          <div class="ml-2">
            <v-card-text
              style="padding: 0px !important; font-size: 18px !important"
            >
              <strong>Payment Type:</strong> {{ currentPayment }}</v-card-text
            >
            <v-card-text
              style="padding: 0px !important; font-size: 18px !important"
            >
              <strong>Total:</strong> ${{ total }}</v-card-text
            >
          </div>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="#704232" text @click="dialog = false">
              Disagree
            </v-btn>
            <v-btn color="#704232" text @click="agree"> Agree </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-navigation-drawer>
</template>

<script>
// import Modal from "./Modal.vue";
export default {
  data() {
    return {
      currentPayment: "Cash",
      dialog: false,
    };
  },
  components: {
    // Modal,
  },
  paymentTypes: [
    {
      iconName: "fas fa-money-bill-wave",
      title: "Cash",
    },
    {
      iconName: "fas fa-credit-card",
      title: "Debit Card",
    },
    {
      iconName: "fas fa-qrcode",
      title: "E-Wallet",
    },
  ],
  computed: {
    cart() {
      return this.$store.state.cart.cart;
    },
    subTotal() {
      return parseFloat(
        this.cart.reduce((acc, item) => acc + item.priceDynamic * item.count, 0)
      ).toFixed(2);
    },
    tax() {
      return parseFloat(this.subTotal * 0.1).toFixed(2);
    },
    total() {
      return (parseFloat(this.tax) + parseFloat(this.subTotal)).toFixed(2);
    },
  },
  methods: {
    deleteFromCart(id) {
      this.$store.dispatch("deleteFromCart", id);
    },
    addAmount(id) {
      this.$store.dispatch("addAmount", id);
    },
    removeAmount(id) {
      this.$store.dispatch("removeAmount", id);
    },
    addPayment(toggle, payment) {
      toggle();
      this.currentPayment = payment;
    },
    agree() {
      this.dialog = false;
      this.$store.dispatch("addHistory", this.cart);
      this.$store.dispatch("removeCart");
      console.log(this.$store.state.historyCart.historyCart);
    },
  },
};
</script>

<style>
.v-card.borderme {
  border: 2px solid #704232 !important;
}
.col-12 {
  padding: 5px !important;
}
.v-btn.widthoutupercase {
  text-transform: none !important;
}
.small-btn {
  height: 12px !important;
  min-width: 12px !important;
  padding: 0px !important;
  margin-bottom: auto;
  border: 5px solid #704232;
}
.small-btn-item {
  margin: 2px;
}
</style>
