<template>
  <div>
    <v-row
      ><v-col cols="12" sm="6" v-for="(current, i) in currentMenu" :key="i">
        <v-card flat class="rounded-lg mx-5">
          <v-list-item three-line>
            <v-list-item-avatar rounded size="120" class="grey lighten-4">
              <v-img :src="`${current.img}`"></v-img>
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title class="text-h5">
                {{ current.title }}
              </v-list-item-title>
              <v-list-item-subtitle class="mt-1">
                {{ current.description }}
              </v-list-item-subtitle>
              <strong class="mt-3">$
                {{ price ? price : current.priceDynamic }}</strong
              >
            </v-list-item-content>
          </v-list-item>
          <v-card-actions>
            <v-row>
              <v-col
                v-if="current.category === 'Coffee'"
                cols="12"
                sm="6"
                class="center mt-1"
              >
                <strong class="ml-3">Mood</strong> <br />
                <v-item-group mandatory class="mt-n1">
                  <v-container>
                    <v-row>
                      <v-col
                        cols="12"
                        md="4"
                        v-for="(temp, i) in $options.temps"
                        :key="i"
                      >
                        <v-item v-slot="{ active, toggle }">
                          <v-card
                            color="#F6EFEF"
                            :class="active ? 'borderme' : ''"
                            class="d-flex align-center rounded-circle"
                            dark
                            height="50"
                            width="50"
                            @click="
                              () => {
                                toggle();
                                product.temp =
                                  temp.type === 'fas fa-fire' ? 'Hot' : 'Cold';
                              }
                            "
                            flat
                          >
                            <v-list-item class="text-center">
                              <v-list-item-content>
                                <v-icon dark :color="`${temp.color}`">{{
                                  temp.type
                                }}</v-icon>
                              </v-list-item-content>
                            </v-list-item>
                          </v-card>
                        </v-item>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-item-group>
              </v-col>
              <v-col
                v-if="current.category !== 'Dessert'"
                cols="12"
                sm="6"
                class="center mt-1"
              >
                <strong class="ml-2">Size</strong>
                <v-item-group mandatory class="mt-n1">
                  <v-container>
                    <v-row>
                      <v-col
                        cols="12"
                        sm="4"
                        v-for="(size, i) in $options.sizes"
                        :key="i"
                      >
                        <v-item v-slot="{ active, toggle }">
                          <v-card
                            color="#F6EFEF"
                            :class="active ? 'borderme' : ''"
                            class="d-flex align-center rounded-circle"
                            height="50"
                            width="50"
                            @click="addSize(toggle, product, size, current)"
                            flat
                          >
                            <v-list-item class="text-center">
                              <strong>{{ size.title }}</strong>
                            </v-list-item>
                          </v-card>
                        </v-item>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-item-group>
              </v-col>

              <v-col
                v-if="
                  current.category !== 'Dessert' && current.category !== 'Juice'
                "
                cols="12"
                sm="6"
                class="center mt-1"
              >
                <strong class="ml-3">Sugar</strong> <br />
                <v-item-group mandatory class="mt-n1">
                  <v-container>
                    <v-row>
                      <v-col
                        cols="12"
                        md="4"
                        v-for="(sugar, i) in $options.sugars"
                        :key="i"
                      >
                        <v-item v-slot="{ active, toggle }">
                          <v-card
                            color="#F6EFEF"
                            :class="active ? 'borderme' : ''"
                            class="d-flex align-center rounded-circle"
                            dark
                            height="50"
                            width="50"
                            @click="
                              () => {
                                toggle();
                                product.sugar = sugar.title;
                              }
                            "
                            flat
                          >
                            <v-list-item class="text-center">
                              <v-list-item-content>
                                <strong
                                  class="overline"
                                  :class="active ? 'brown--text' : 'red--text'"
                                  >{{ sugar.title }}</strong
                                >
                              </v-list-item-content>
                            </v-list-item>
                          </v-card>
                        </v-item>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-item-group>
              </v-col>
            </v-row>
          </v-card-actions>
          <v-card-actions>
            <v-btn
              color="#704232"
              block
              dark
              class="widthoutupercase mb-2"
              @click="addToCart(product, current)"
            >
              Add Billing
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  props: ["currentMenu"],
  data() {
    return {
      product: {},
      price: 0,
    };
  },
  temps: [
    {
      type: "fas fa-fire",
      color: "orange",
    },
    {
      type: "fab fa-modx",
      color: "blue",
    },
  ],
  sizes: [
    {
      title: "S",
    },
    {
      title: "M",
    },
    {
      title: "L",
    },
  ],
  ices: [
    {
      title: "30",
    },
    {
      title: "50",
    },
    {
      title: "70",
    },
  ],
  sugars: [
    {
      title: "30",
    },
    {
      title: "50",
    },
    {
      title: "70",
    },
  ],
  methods: {
    addToCart(product, current) {
      const result = Object.assign({}, current, product);
      result.id = Date.now();
      this.$store.dispatch("addToCart", result);
    },
    addSize(toggle, product, size, current) {
      toggle();
      product.size = size.title;
      if (product.size === "M") {
        current.priceDynamic = +(
          (parseFloat(current.price) * 1.33 * 100) /
          100
        ).toFixed(2);
        product.priceDynamic = current.priceDynamic;
      } else if (product.size === "L") {
        current.priceDynamic = +(
          (parseFloat(current.price) * 1.66 * 100) /
          100
        ).toFixed(2);
        product.priceDynamic = current.priceDynamic;
      } else {
        current.priceDynamic = +current.price;
        product.priceDynamic = current.priceDynamic;
      }
    },
  },
};
</script>

<style>
.center {
  margin: 0 auto !important;
}
</style>
