const mutations = {
    setHistory(state, order) {
        console.log(order)
      state.historyCart.push(order);
    },
    setDeleteHistory(state, id) {
      state.historyCart = state.history.filter((item) => item.id !== id);
    },
  };
  
  const actions = {
    addHistory({ commit }, order) {
      commit("setHistory", order);
    },
    deleteHistory({ commit }, id) {
      commit("setDeleteHistory", id);
    }
  };
  
  const getters = {
    historyCart: ({ historyCart }) => historyCart,
  };
  
  const state = () => ({
    historyCart: [],
  });
  
  export default {
    mutations,
    getters,
    actions,
    state,
  };
  