<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background }">
    <SidebarRight />
    <v-container>
      <Categories @showMenu="showMenu" @inputSearch="inputSearch" />
      <v-toolbar color="#EEEEEE" flat>
        <v-toolbar-title>{{ id ? id : "" }} Menu</v-toolbar-title>
        <v-spacer></v-spacer>
        <span color
          >{{ menu ? menu.length : "" }} {{ id ? id : "" }} Result</span
        >
      </v-toolbar>
      <CurrentMenu :currentMenu="menu" />
    </v-container>
  </v-app>
</template>

<script>
import SidebarRight from "../components/SidebarRight.vue";
import Categories from "../components/Categories.vue";
import CurrentMenu from "../components/CurrentMenu.vue";
export default {
  name: "Home",

  components: {
    SidebarRight,
    Categories,
    CurrentMenu,
  },
  data() {
    return {
      currentMenu: "",
      id: "",
    };
  },
  created() {
    this.showMenu("all");
  },
  computed: {
    menu() {
      return this.currentMenu;
    },
  },
  methods: {
    showMenu(id) {
      this.currentMenu = [];
      if (id === "all") {
        this.id = "All";
        this.$store.state[`coffies`][`coffies`].map((i) =>
          this.currentMenu.push(i)
        );
        this.$store.state[`juices`][`juices`].map((i) =>
          this.currentMenu.push(i)
        );
        this.$store.state[`milk_baseds`][`milk_baseds`].map((i) =>
          this.currentMenu.push(i)
        );
        this.$store.state[`desserts`][`desserts`].map((i) =>
          this.currentMenu.push(i)
        );
      } else {
        this.currentMenu = this.$store.state[`${id}`][`${id}`];
        this.id = this.currentMenu[0].category;
      }
    },
    inputSearch(inputData) {
      this.currentMenu = [];
      this.id = "";
      this.$store.state[`coffies`][`coffies`].map((i) => {
        if (i.title.toLowerCase().includes(inputData.toLowerCase()))
          this.currentMenu.push(i);
      });
      this.$store.state[`juices`][`juices`].map((i) => {
        if (i.title.toLowerCase().includes(inputData.toLowerCase()))
          this.currentMenu.push(i);
      });
      this.$store.state[`milk_baseds`][`milk_baseds`].map((i) => {
        if (i.title.toLowerCase().includes(inputData.toLowerCase()))
          this.currentMenu.push(i);
      });
      this.$store.state[`desserts`][`desserts`].map((i) => {
        if (i.title.toLowerCase().includes(inputData.toLowerCase()))
          this.currentMenu.push(i);
      });
    },
  },
};
</script>
<style>
.v-card.borderme {
  border: 2px solid #704232 !important;
}
.v-card.borderout {
  border: 1px solid #bcaaa4 !important;
}
</style>
